.header__nav {
  height: 105px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 670px;
}
@media (max-width: 1200px) {
  .header__nav {
    width: 570px;
  }
}
@media (max-width: 768px) {
  .header__nav {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    opacity: 0;
    background: url(../img/banner/bg-img.png) no-repeat center/cover;
    padding-top: 85px;
    justify-content: flex-start;
  }
}
.header__nav::after {
  position: absolute;
  display: block;
  content: "";
  background: #252B49;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.8;
  visibility: hidden;
}
@media (max-width: 768px) {
  .header__nav::after {
    visibility: visible;
  }
}
.header__nav .nav__item {
  position: relative;
  padding-top: 46px;
  height: 100%;
  width: 20%;
  text-align: center;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 700;
  color: #FFFFFF;
}
.header__nav .nav__item::after {
  content: "";
  position: absolute;
  top: calc(100% - 3px);
  left: 0;
  display: block;
  width: 100%;
  height: 3px;
  background-color: #BABBC5;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
.header__nav .nav__item:hover::after {
  opacity: 1;
}
.header__nav .nav__item:active::after {
  opacity: 1;
  background-color: #E71414;
}
.header__nav .nav__item:active {
  color: #E71414;
}
@media (max-width: 768px) {
  .header__nav .nav__item {
    width: 100%;
    height: 65px;
    padding: 25px 15px;
    text-align: left;
    z-index: 6;
  }
}

/*burger menu*/
#menu__toggle {
  opacity: 0;
  position: absolute;
  top: 22px;
  right: 40px;
  z-index: 5;
}

.menu__btn {
  display: flex;
  align-items: center;
  position: absolute;
  top: 19px;
  right: 35px;
  width: 24px;
  height: 18px;
  cursor: pointer;
  visibility: hidden;
}
@media (max-width: 768px) {
  .menu__btn {
    visibility: visible;
    z-index: 5;
  }
}

.menu__btn > span,
.menu__btn > span::before,
.menu__btn > span::after {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #FFFFFF;
}

.menu__btn > span::before {
  content: "";
  top: -7px;
}

.menu__btn > span::after {
  content: "";
  top: 7px;
}

#menu__toggle:checked ~ .menu__btn > span {
  transform: rotate(45deg);
}

#menu__toggle:checked ~ .menu__btn > span::before {
  top: 0;
  transform: rotate(0);
}

#menu__toggle:checked ~ .menu__btn > span::after {
  top: 0;
  transform: rotate(90deg);
}

#menu__toggle:checked ~ nav {
  opacity: 1;
}

#menu__toggle:checked ~ #top {
  opacity: 0;
}

