  .swiper-pagination-bullets.swiper-pagination-horizontal,
  .swiper-pagination-custom,
  .swiper-pagination-fraction {
    bottom: 0px;
  }
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 12.5px);
}

.swiper-pagination-bullet {
  width: var(--swiper-pagination-bullet-width, var(--swiper-pagination-bullet-size, 14px));
  height: var(--swiper-pagination-bullet-height, var(--swiper-pagination-bullet-size, 14px));
  background: var(--swiper-pagination-bullet-inactive-color, #FFFFFF);
  opacity: var(--swiper-pagination-bullet-inactive-opacity, 1);
  border: 1px solid #7E5D6F;
}
@media (max-width: 1200px) {
  .swiper-pagination-bullet {
    width: var(--swiper-pagination-bullet-width, var(--swiper-pagination-bullet-size, 9.5px));
    height: var(--swiper-pagination-bullet-height, var(--swiper-pagination-bullet-size, 9.5px));
  }
}

.swiper-pagination-bullet-active {
  background: var(--swiper-pagination-bullet-inactive-color, #7E5D6F);
}

